@import "dev/scss/helpers/variables";
@import "dev/scss/helpers/mixins";

.basket-list {

	.basket-list__footer {
		margin-top: 8px;
	}
}

.basket-list__item {

	& + & {
		margin-top: 8px;
	}
}

.basket-btn {
	border-radius: 10px;
	height: 44px;
	line-height: 42px;
	padding: 0 12px;
	font-size: 13px;
}


/**
* Group
*/

.basket-group {
	background-color: rgba(37, 40, 44, 0.05);
	border-radius: 10px;

	& + & {
		margin-top: 8px;
	}

	.controls-list__item {
		display: none;
	}

	.basket-list__item {
		border-bottom: 0.5px solid rgba(0, 0, 0, 0.1);

		@include g-transition(margin);
	}

	.basket-list__item + .basket-list__item {
		margin-top: 0;
	}

	.basket-list__footer {
		margin-top: 0;
	}

	.item-row__inner {
		background-color: transparent;
	}

	.item-row_active .item-row__inner {
		background-color: #24272E;
		border-radius: 0;
	}

	.basket-btn {
		border-radius: 0;
		background-color: transparent;
	}
}

.basket-group__head {
	padding: 9px 12px;
	border-bottom: 0.5px solid rgba(0, 0, 0, 0.1);
}

.basket-group[data-state="default"] {

	.controls-list__item[data-display-state="default"] {
		display: inline-block;
	}
}

.basket-group[data-state="edit"] {

	.controls-list__item[data-display-state="edit"] {
		display: inline-block;
	}

	.basket-list__item_regular {
		margin-left: 56px;
	}

	.item-row {
		position: relative;
	}

	.item-row__aside {
		display: flex;
		flex-wrap: wrap;
		align-items: center;
		position: absolute;
		font-size: 0;
		right: 100%;
		top: 0;
		height: 100%;
		padding: 12px;
		text-align: center;
	}
}

.order-section__ctrl .c-btn_order {
	height: 44px;
	line-height: 42px;
}


/**
* Shopping list
*/

.shopping-group {

	& + & {
		margin-top: 8px;
	}
}

.shopping-list__item {
	margin-left: -20px;
	margin-right: -20px;
	padding-left: 20px;
	padding-right: 20px;

	.u-row {
		margin-left: -5px;
		margin-right: -5px;
		height: 40px;
		line-height: 40px;
	}

	.u-col {
		padding-left: 5px;
		padding-right: 5px;
	}
}

.shopping-list__item_control-empty {
	background-color: rgba(37, 40, 44, 0.05);
	color: $color-active-gray;
}

.shopping-list__value {
	// font-size: 13px;
	// line-height: 16px;
	font-size: 12px;
	// letter-spacing: -.3px;

	text-align: right;
}

.shopping-list_quantity {

	.shopping-list__title {
		flex-grow: 1;
	}

	.shopping-list__quantity,
	.shopping-list__value {
		width: 112px;
		text-align: right;
	}

	.c-status {
		margin-right: 5px;
	}
}

.shopping-list__item_editmode {
	background-color: #fff;
	// box-shadow: 0px 60px 30px rgba(0, 0, 0, 0.1), 0px 30px 60px rgba(37, 40, 44, 0.3);
	color: #000;

	// .shopping-list__value {
	// 	outline: 0;
	// }

	.c-input {

		@include input-placeholder {
			color: #C9CDD1 !important;
			// color: transparent;
		}
	}
}

.shopping-list__item_multiple-inputs {

	.shopping-list__quantity {

		.c-input {

			@include input-placeholder {
				// color: #C9CDD1;
				color: transparent;
			}
		}
	}

	// &.shopping-list__item_control {

	// 	// .shopping-list__quantity {
	// 	// 	font-size: 9px;
	// 	// }
	// }

	// &.shopping-list__item_editmode {

	// 	// .shopping-list__quantity {
	// 	// 	font-size: 12px;
	// 	// }
	// }
}

.shopping-list__value {

	.c-input {

		@include input-placeholder {
			// color: #C9CDD1;
			color: transparent;
		}
	}
}

.draggable {

	@include g-transition(transform);
}

.dragging {
	position: relative;
	z-index: 1;
	background: $color-action;

	.item-row__inner {
		background: #fff;
		transform: rotate(2deg) translate(12px, -12px); // немного уменьшен отступ слева по сравнению с макетом, чтобы элемент списка не вылезал за рамки
		box-shadow: 0px 60px 30px rgba(0, 0, 0, 0.1), 0px 30px 60px rgba(37, 40, 44, 0.3);
	}
}
