@import "dev/scss/helpers/variables";
@import "dev/scss/helpers/mixins";

.c-clock {
	background-color: rgba(37, 40, 44, 0.1);
	border-radius: 10px;
	backdrop-filter: blur(120px);
	text-align: center;
	padding: 20px 10px 16px;
	margin-bottom: 8px;
}

.c-clock__title {
	font-size: 36px;
	line-height: 1;
}

.c-clock__desc {
	font-size: 13px;
	line-height: 16px;
	margin-top: 8px;
}
