@import "dev/scss/helpers/variables";
@import "dev/scss/helpers/mixins";

.controls-menu {
	min-width: 200px;
}

.controls-menu__item_active {

	.controls-menu__link {
		background: rgba(247, 248, 250, 0.7);
		backdrop-filter: blur(60px);
		border-radius: 10px;
	}
}

.controls-menu__link {
	display: block;
	color: #24272E;
	text-decoration: none;
	position: relative;
	height: 44px;
	font-size: 13px;
	font-weight: bold;
	padding: 0 18px;
	display: inline-flex;
	align-items: center;
	width: 100%;
}

.controls-menu__icon {
	width: 20px;
	height: 20px;
	margin-right: 16px;
}


/**
* Separator
*/

.controls-menu__item_separator {
	margin-top: 32px;
	position: relative;

	&:before {
		content: '';
		position: absolute;
		top: -16px;
		left: 0;
		right: 0;
		border-top: 0.5px solid rgba(0, 0, 0, 0.1);
	}
}
