@import "dev/scss/helpers/variables";
@import "dev/scss/helpers/mixins";

.c-toggle {
	background-color: rgba(37, 40, 44, 0.1);
	border-radius: 6px;
	padding: 1px;
	display: inline-flex;
	max-width: 100%;
	overflow: hidden;
}

.c-toggle__item {
	flex: 0 1 auto;
	max-width: 100%;
	min-width: 0;
}

.c-toggle__label {
	outline: 0;
}

.c-toggle__text {
	display: block;
	height: 26px;
	line-height: 26px;
	border-radius: 5px;
	padding: 0 12px;
	min-width: 66px;
	text-align: center;
	text-overflow: ellipsis;
	overflow: hidden;
	white-space: nowrap;
}

.c-toggle__input:checked + .c-toggle__text {
	background: #fff;
}
