@import "dev/scss/helpers/variables";
@import "dev/scss/helpers/mixins";
@import "dev/scss/helpers/functions";

.c-panel__head-top {

	&.u-row {
		margin-left: -5px;
		margin-right: -5px;
	}

	& > .u-col {
		padding-left: 5px;
		padding-right: 5px;
	}
}

/**
* panel btn
*/

.panel-btn {
	width: 28px;
	height: 28px;
	border-radius: 50%;
	background-color: rgba(37, 40, 44, 0.1);
	color: #24272E;
	position: relative;

	// &:hover, &:focus {
	// 	background-color: ;
	// 	color: #fff;
	// }
}

.panel-btn__icon {
	width: 14px;
	height: 14px;
}

.c-panel {
	height: 100%;
	padding: 16px 12px;

	display: flex;
	flex-direction: column;
	overflow: hidden scroll;
	// flex-wrap: wrap;

	@include hide-scrollbar;
}

.c-panel__head {
	padding-left: 12px;
	// padding-right: 12px;
	margin-bottom: 12px;

	flex: 0 0 auto;
	max-width: 100%;
}

.c-panel__head-top {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-between;
}

.c-panel__tablist {
	margin-top: 16px;
}

.c-panel__body {
	flex: 1 0 auto;
	max-width: 100%;
	position: relative;
}

.c-panel__body_empty {
	display: flex;
	flex-direction: column;
	flex-wrap: wrap;
}

.c-panel__footer {
	flex: 0 0 auto;
	max-width: 100%;
}


/**
* Aside
*/

.c-panel_side {
	position: absolute;
	width: 60px;
	height: 100%;
	// overflow: hidden;
	top: 0;
	left: 0;
	z-index: 1;
	background: rgba(247, 248, 250, 0.7);
	backdrop-filter: blur(120px);
	box-shadow: 0px 30px 60px rgba(37, 40, 44, 0.2), 0px 2px 4px rgba(37, 40, 44, 0.02);
	padding: 16px 12px;
	border-top-right-radius: 16px;

	.c-panel__head {
		padding-left: 0;
		margin-bottom: 0;

		.controls-list {
			text-align: center;
		}

		.controls-list__item {
			width: 100%;
			margin-bottom: 12px;
		}
	}
}

.c-panel_side-alt {
	background-color: rgba(149, 154, 160, 0.15);
	border-radius: 0;
	box-shadow: none;
}


/**
* Notifications
*/

.c-panel_notifications {
	border-top-right-radius: 16px;
	background: rgba(255, 255, 255, 0.5);
	box-shadow: 0px 20px 40px rgba(37, 40, 44, 0.1), 0px 2px 4px rgba(37, 40, 44, 0.02);
	backdrop-filter: blur(120px);
	padding-left: 78px;
}


/**
* Slide in
*/

.c-panel_slide-in[data-direction="rtl"] {
	position: absolute;
	right: 0;
	top: 0;
	bottom: 0;
	z-index: 1;
	transform: translateX(100%);
	opacity: 0;

	border-top-left-radius: 16px;
	// background: rgba(255, 255, 255, 0.5);
	background: rgba-to-rgb(rgba(255, 255, 255, 0.5), #C8C8CC);
	box-shadow: 0px 20px 40px rgba(37, 40, 44, 0.1), 0px 2px 4px rgba(37, 40, 44, 0.02);
	backdrop-filter: blur(120px);
	padding-right: 170px;
	min-width: 400px;
	max-width: 80%;

	@include g-transition(transform .3s, opacity .3s);

	&.c-panel_slided {
		transform: translateX(0);
		opacity: 1;
	}
}

.c-panel#menubar {
	width: 474px;
	display: flex;
	flex-wrap: wrap;
	padding: 0 0 0 45px;
	overflow: hidden; // скрываем скролл для панели

	.c-panel__aside {
		width: 46px;
		position: absolute;
		top: 0;
		left: 0;
		height: 100%;
		background-color: #CBCCCE;
		overflow: hidden scroll; // отдельный скролл для колонки

		@include hide-scrollbar;
	}

	.c-panel__inner {
		height: 100%;
		padding: 16px 170px 16px 12px;
		box-shadow: -2px 2px 4px rgba(37, 40, 44, 0.02), -10px 30px 60px rgba(37, 40, 44, 0.1);
		overflow: hidden scroll; // отдельный скролл для колонки

		@include hide-scrollbar;
	}

	.side-menu {
		display: flex;
		flex-direction: column;
		justify-content: center;
		height: 100%;
	}
}

.c-panel#accountbar {
	width: 428px;

	.c-panel__head.u-row {
		margin-left: -5px;
		margin-right: -5px;
		padding-left: 0;
		padding-right: 0;

		& > .u-col {
			padding-left: 5px;
			padding-right: 5px;
		}
	}

	.panel-btn {
		background: none !important;
		border-radius: 0;
	}

	.panel-btn__icon {
		width: 24px;
		height: 24px;
	}
}
