@import "dev/scss/helpers/variables";
@import "dev/scss/helpers/mixins";

.orders-list {
	position: relative;
}

.orders-list_active {

	.popup-menu {
		display: block;
		z-index: 100;
	}
}

.orders-list__item {

	& + & {
		margin-top: 8px;
	}
}

.order-item {
	background: #fff;
	backdrop-filter: blur(60px);
	border-radius: 10px;
	padding: 9px 12px 12px;
	min-height: 134px;

	@include g-transition(opacity);
}

.order-item__top {
	margin-bottom: 7px;
}

.order-item__status {
	font-size: 14px;
	line-height: 1;
}

.order-item__title {
	margin-bottom: 4px;
}

.order-item__clients {
	margin-bottom: 8px;
}

.order-item__total {
	margin-bottom: 12px;
}

.order-item__sum {
	margin-bottom: 10px;
}

.order-item__taglist {

	& + & {
		margin-top: 4px;
	}
}


/**
* Active
*/

.order-item.c-overlay__highlight,
.order-item_active {
	box-shadow: 0px 2px 4px rgba(37, 40, 44, 0.02), 0px 40px 80px rgba(37, 40, 44, 0.3);
}


/**
* Dimmed
*/

.order-item_dimmed {
	opacity: 0.3;
}


/**
* Kitchen
*/

.order-item_kitchen {
	position: relative;
	padding-top: 28px;
	padding-bottom: 10px;
	overflow: hidden;

	.order-item__time {
		font-size: 11px;
	}

	.progressbar {
		position: absolute;
		top: 0;
		left: 0;
		right: 0;
	}

	.order-item__title {
		font-size: 15;
		letter-spacing: .1px;
		font-weight: 600;
	}
}

.c-waiter {
	font-size: 11px;
	font-weight: 500;
	line-height: 12px;
	text-transform: uppercase;
	display: flex;
	align-items: center;
}

.c-waiter__icon {
	width: 8px;
	height: 8px;
	margin-right: 3px;
	stroke: $color-active-gray
}


/**
* Kitchen Income
*/

.order-item_kitchen-income {
	background-color: rgba(255,255,255,.3);
	padding-top: 10px;
	padding-bottom: 10px;

	&.order-item_error {
		background-color: $color-error-dark;
		color: #D2D3D6;
	}

	&.order-item_warning {
		background-color: $color-warning;
	}

	&.order-item_success {
		background-color: $color-success;
	}

	.order-item__top {
		margin-left: -5px;
		margin-right: -5px;
		line-height: 1;

		& > .u-col {
			padding-left: 5px;
			padding-right: 5px;
		}
	}

	.order-item__time {
		font-size: 11px;
		line-height: 12px;
	}

	.progressbar__timer {
		font-size: 11px;
		line-height: 12px;
	}

	.order-item__waiter {
		margin-left: auto;
		color: #000;
	}

	.order-item__title {
		font-size: 15px;
		line-height: 18px;
		letter-spacing: .1px;
		font-weight: 600;
	}
}


/**
* Free
*/

.order-item_free {
	min-height: 134px;
	display: flex;
	flex-direction: column;

	.order-item__tags {
		margin-top: auto;
	}
}
