@import "dev/scss/helpers/variables";
@import "dev/scss/helpers/mixins";

.custom-input__decor {
	display: flex;
	justify-content: center;
	padding: 12px 0;
}

.custom-input__mark {
	width: 20px;
	height: 20px;
	color: rgba(#fff, .1);
	display: inline-block;
	margin: 0 16px;
}

.custom-input__mark_active {
	color: rgba(#fff, 1);
}

.custom-input__mark-icon {
	stroke: none;
}

$states: (
	('success', $color-success),
	('error', $color-error)
);

@mixin color-state($palette) {

	@each $modifier, $color in $palette {

		.custom-input_#{$modifier} {
			color: $color;

			.custom-input__mark_active {
				color: inherit !important;
			}
		}
	}
}

@include color-state($states);
