@import "dev/scss/helpers/variables";
@import "dev/scss/helpers/mixins";

$counter-height: 20px;

$border-width: 1px;
$border-color: #f3f3f3;

$btn-width: 24px;

$input-width: 36px;

.c-counter {
	display: inline-block;
	font-size: 0;
	height: $counter-height;
	white-space: nowrap;
	background: #fff;
	border: $border-width solid $border-color;
}

.c-counter__input,
.c-counter__btn {
	font-size: 14px;
	border: 0;
	border-radius: 0;
	outline: 0;
	display: inline-block;
	vertical-align: middle;
	height: 100%;
	text-align: center;
}

.c-counter__input {
	color: $text-color;
    width: $input-width;
	padding: 0 7px;
	font-size: $global-font-size;
}

.c-counter__btn {
	position: relative;
    padding: 0 2px;
	width: $btn-width;
	border: 0;
	text-align: center;
	background: #f3f3f3;
	user-select: none;
	line-height: $counter-height - $border-width*2;

	@include g-transition(background-color);

	&:hover, &:focus {
        background-color: #e0e0e0;
	}

	&.c-counter__btn_disabled {
		opacity: 0.7;
		cursor: default;

		&:hover, &:focus {
			background-color: #f3f3f3;
		}
	}
}

.c-counter__measure {
	display: inline-block;
	vertical-align: middle;
    min-width: 28px;
    padding: 0 2px;
    text-align: center;
	color: $text-color;
	font-size: $global-font-size;
	user-select: none;
}
