@import "dev/scss/helpers/variables";
@import "dev/scss/helpers/mixins";
@import "dev/scss/helpers/functions";

$ico-width: 20px;
$ico-height: 20px;

$border-color: $color-active-gray;

.c-ctrl {
	margin-bottom: 10px;
	text-align: left;
	display: inline-block;

	&:last-child {
		margin-bottom: 0;
	}
}

.c-ctrl__input {

	@include hide;

    &:focus + .c-ctrl__title:before {
        border-color: $color-error;
    }
}

.c-ctrl__title {
	font-size: 14px;
	display: block;
	position: relative;
	line-height: $ico-height;
	font-weight: normal;
	position: relative;
	padding-left: 28px;

    &:before {
        content: '';
        width: $ico-width;
        height: $ico-height;
		border: 1px solid $border-color;
		border-radius: 50px;
		position: absolute;
		top: 0;
		left: 0;
		background-size: 12px;
		background-repeat: no-repeat;
		background-position: center;

		@include g-transition(border-color);
	}
}

.c-ctrl_icon-only {

	.c-ctrl__title {
		padding: 0;

		&:before {
			position: static;
			display: block;
		}
	}
}


