@import "dev/scss/helpers/variables";
@import "dev/scss/helpers/mixins";

.options-list__list {
	margin-left: -4px;
	margin-right: -4px;
}

.options-list__item {
	padding-left: 4px;
	padding-right: 4px;
}

.options-list__label {
	display: block;
	height: 36px;
	width: 36px;
	border: 0.5px solid rgba(0, 0, 0, 0.1);
	border-radius: 6px;
	display: flex;
	justify-content: center;
	align-items: center;
}

.options-list__input:checked + .options-list__label {
	background-color: $color-action;
	border-color: $color-action;
	color: #fff;
}
