@import "dev/scss/helpers/variables";
@import "dev/scss/helpers/mixins";

.tracker-panel {
	position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
	text-align: center;
	background: #24272E;
	color: #fff;
	z-index: 99;
	padding: 80px 0 0 0;

	&[data-panel="open"] {

		.tracker-panel__title {
			margin-top: 92px;
			margin-bottom: 8px;
		}
	}
}

.tracker-panel__title {
	margin-bottom: 32px;
}

.tracker-panel__controls {
	margin-top: 64px;
	text-align: center;

	.c-btn {
		max-width: 244px;
	}
}


/**
* Timers list
*/

.timers-list {

}

.timers-list__label {
	display: block;
	background: rgba(255, 255, 255, 0.1);
	backdrop-filter: blur(120px);
	border-radius: 10px;
	padding: 32px 10px;
	min-width: 144px;
}

.timers-list__title {
	display: block;
	margin-bottom: 8px;
}

.timers-list__subtitle {
	display: block;
}

.timers-list__input:checked ~ .timers-list__label {
	background-color: $color-action;
}


/**
* Secondary
*/

.timers-list__label_secondary {
	background: 0;
}

.timers-list__label_dimmed {
	opacity: 0.3;
}

.c-overlay__btn {
	width: 28px;
	height: 28px;
	background: rgba(255, 255, 255, 0.1);
	backdrop-filter: blur(60px);
	border-radius: 50%;
	color: #fff;
	position: absolute;
	top: 20px;
	right: 20px;
	outline: 0;
	font-size: 0;
}

.c-overlay__btn-icon {
	width: 10px;
	height: 10px;
	stroke-width: .2;
}

.c-countdown[data-progressbar] {
	background: 0;

	.progressbar__timer {
		color: inherit;
	}
}
