@import "dev/scss/helpers/variables";
@import "dev/scss/helpers/mixins";

.item-row {
	text-decoration: none;
	outline: 0;
	position: relative;

	&.u-row {
		margin-left: 0;
		margin-right: 0;
	}
}

.item-row__inner {
	background: rgba(37, 40, 44, 0.05);
	border-radius: 10px;
	padding: 12px 12px;
	color: #24272E;

	flex-grow: 1;
}

.item-row__aside {
	display: none;
}

.item-row__body {
	// height: 40px;
	// line-height: 40px;

	&.u-row {
		margin-left: -5px;
		margin-right: -5px;
	}

	.u-col {
		padding-left: 5px;
		padding-right: 5px;
	}
}

.item-row__footer {
	margin-top: 4px;
	color: #99A1AD;
}

.item-row__title {
	flex-grow: 1;

	.inline-list {
		color: #24272E;
	}
}

.item-row__time {
	font-size: 9px;
	line-height: 12px;
	font-weight: 600;
	letter-spacing: .1px;
	color: #99A1AD;
	text-transform: uppercase;
	width: 66px;
	text-align: right;
}

.item-row__price {
	font-size: 12px;
	line-height: 12px;
	font-weight: 500;
	width: 66px;
	text-align: right;
}

.item-row_active {

	.item-row__inner {
		background: #24272E;
		box-shadow: 0px 20px 40px rgba(37, 40, 44, 0.1), 0px 2px 4px rgba(37, 40, 44, 0.02);
		color: #DFDFDF;
	}

	.item-row__title {
		color: inherit;
	}

	.item-row__title .inline-list {
		color: inherit;
	}

	.item-row__footer {
		color: #86898F;
	}
}

.item-row_error {

	.item-row__inner {
		background-color: $color-error;
		color: #fff;
	}

	.item-row__title .inline-list {
		color: inherit;
	}
}

.item-row__status {
	font-size: 12px;
	line-height: 1;
	font-weight: 500;
	letter-spacing: -.1px;
}

/**
* Heading
*/

.item-row_heading {

	.item-row__inner {
		background-color: #6D7685;
		color: #fff;
	}
}

.item-row_heading-alt {

	.item-row__inner {
		background-color: rgba(255, 255, 255, 0.1);
	}
}

.item-row__aside-right {
	overflow: hidden;
	position: absolute;
	opacity: 0;
	height: 1px;

	.controls-panel {
		padding-left: 16px;
		height: 100%;
	}

	.controls-list {
		height: 100%;
		margin-left: -5px;
		margin-right: -5px;
		flex-wrap: nowrap;
	}

	.controls-list__item {
		padding-left: 5px;
		padding-right: 5px;
	}

	&.u-col {
		padding-left: 0;
		padding-right: 0;
	}
}

.item-row__btn {
	width: 32px;
	height: 32px;
	border-radius: 50%;
	position: relative;
	overflow: hidden;
}

.item-row__btn-icon {
	width: 16px;
	height: 16px;
}

.item-row__btn.item-row__btn_delete {
	background-color: $color-error;
	color: #fff;
}


/**
* Menu Item
*/

.item-row[data-menu-item] {
	background-color: #DFDFDF;
	border-radius: 6px;
}
