@import "dev/scss/helpers/variables";
@import "dev/scss/helpers/mixins";

.auth-form {
	height: 100vh;
	width: 100%;
	position: relative;
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
	background-image: url('../images/bg-01.jpg');
	flex: 0 0 auto;

	.login-block {
		margin-top: 96px;
	}
}

.auth-form__title {
	font-size: 24px;
	line-height: 28px;
	margin-bottom: 20px;
}

.auth-form__controls {
	position: absolute;
	bottom: 0;
	right: 0;
	padding: 20px;

	.u-row {
		margin-left: -20px;
		margin-right: -20px;
	}

	.u-col {
		padding-left: 20px;
		padding-right: 20px;
	}
}

.auth-btn {
	background-color: $color-action;
	outline: 0;
	color: #fff;
	padding: 0;
	height: 56px;
	width: 56px;
	border-radius: 16px;
}

.auth-btn__icon {
	width: 24px;
	height: 24px;
}

.auth-form__panels {
	height: 100%;
}

.auth-form__panel {
	height: 100%;
	text-align: center;
	padding: 80px 0;
	color: #fff;

	&[data-panel="qr"] {
		background-color: rgba(36, 39, 46, 0.96);
		// clip-path: url('#qr-mask');
	}
}

.auth-form__panel_dark {
	background: #24272E;
}


/**
* Code form
*/

.auth-code-form {
	display: inline-block;
	width: 100%;
	max-width: 336px;

	.c-field {
		margin-bottom: 52px;
	}
}
