@import "dev/scss/helpers/variables";
@import "dev/scss/helpers/mixins";

.menu-band {

}

.menu-band__section {

	& + & {
		margin-top: 16px;
	}
}

.menu-section__head {
	margin-bottom: 12px;
	color: $color-action;
	cursor: pointer;

	&.u-row {
		margin-left: 6px;
		margin-right: 6px;
	}

	.u-col {
		padding-left: 6px;
		padding-right: 6px;
	}
}

.menu-section__body {

	@include g-transition(max-height .5s);
}

.menu-section_active {

	.menu-section__head {
		color: #24272E;
	}
}

.menu-list__item {

	& + & {
		margin-top: 8px;
	}
}

.menu-item {
	background: #fff;
	backdrop-filter: blur(60px);
	border-radius: 10px;
	padding: 10px 12px 12px;
}

.menu-item__head {
	margin-bottom: 4px;

	&.u-row {
		margin-left: -3px;
		margin-right: -3px;
	}

	.u-col {
		padding-left: 3px;
		padding-right: 3px;
	}
}

.menu-item__title {
	flex-basis: 75%;
	max-width: 75%;
	// max-width: 80%;

	&.c-title_sm {
		max-height: 32px;
		overflow: hidden;
		text-overflow: ellipsis;
	}
}

.menu-item__price {
	flex-basis: 25%;
	max-width: 25%;
	// max-width: 20%;
}

.menu-item__desc {
	margin-bottom: 12px;
}


/**
* Promo
*/

.menu-item_promo {
	background-color: #FAB037;

	.menu-item__brief {
		color: rgba(0,0,0, .3);
	}
}

.menu-item__brief {
	margin-bottom: 12px;
}
