@import "dev/scss/helpers/variables";
@import "dev/scss/helpers/mixins";

.c-status {
	border: 0;
	text-align: center;
	max-width: 100%;
	white-space: nowrap;
	text-overflow: ellipsis;
	font-weight: 600;
	text-transform: uppercase;
	font-size: 9px;
	line-height: 16px;
	letter-spacing: -.1px;

	display: inline-flex;
	align-items: center;
}

.c-status_icon-before {

	.c-status__icon {
		margin-right: 3px;
	}
}

.c-status_icon-after {

	.c-status__icon {
		margin-left: 3px;
	}
}

.c-status__icon {
	width: 12px;
	height: 12px;
	border-radius: 50%;
	padding: 1px;
}

.c-status_warning {
	// background-color: rgba(235, 120, 5, 0.2);
	color: $color-warning;

	.c-status__icon {
		background-color: rgba($color-warning, 0.2);
	}

	&.c-status_badge {
		background-color: rgba(235, 120, 5, 0.2);
	}
}

.c-status_success {
	color: $color-success;

	.c-status__icon {
		background-color: rgba($color-success, 0.2);
		fill: $color-success;
	}
}
.c-status_error {
	color: $color-error;

	.c-status__icon {
		background-color: rgba($color-error, 0.2);
		fill: $color-error;
	}
}


/**
* Badge
*/

.c-status_badge {
	padding: 0 4px;
	font-weight: 500;
	border-radius: 100px;
}


/**
* Text
*/

.c-status_text {
	font-weight: inherit;
	text-transform: inherit;
}
