/****************************************************************************/
/* ANIMATIONS */
/****************************************************************************/


@mixin toggle-up($speed: .5s) {
    max-height: 0;
    transition: max-height $speed cubic-bezier(0,1,0,1);
	overflow: hidden;
	visibility: hidden;
    will-change: max-height;
    transform: translateZ(0);
}

@mixin toggle-down($height: 1000px, $speed: 1s) {
	max-height: $height;
	visibility: visible;
	transition: max-height $speed ease-in-out;
}

@mixin dropdown-down($shift: -20px, $top: 0, $z-index: 1) {
	display: none;
	position: absolute;
	top: $top;
	left: 0;
	right: 0;
	z-index: $z-index;
}

@mixin dropdown-up() {
	display: block;
	animation: slide-in .15s;
}
