@import "dev/scss/helpers/variables";
@import "dev/scss/helpers/mixins";

$gutter: 12px;

.numpad {

}

.numpad__list {
	margin-left: -$gutter;
	margin-right: -$gutter;
	margin-bottom: -#{$gutter * 2};
}

.numpad__item {
	padding-left: $gutter;
	padding-right: $gutter;
	text-align: center;
	display: flex;
	justify-content: center;
	align-items: center;
	margin-bottom: $gutter * 2;
}

.numpad__btn {
	border: 0;
	outline: 0;
	text-align: center;
	padding: 0;
	position: relative;
	background: 0;
}

.numpad__btn-bg {
	position: absolute;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	stroke: none;
}

.numpad__btn_md {
	width: 56px;
	height: 56px;
	line-height: 56px;
}

.numpad__btn_lg {
	width: 96px;
	height: 96px;
	line-height: 96px;
	font-size: 56px;
}

.numpad__btn-icon {
	width: 30px;
}


/**
* Light
*/

.numpad_light {

	.numpad__btn {
		color: #fff;
	}

	.numpad__btn:active {

		.numpad__btn-bg {
			color: rgba(#fff, .3);
		}
	}

	.numpad__btn-bg {
		color: rgba(#fff, .1);
	}
}


/**
* Small
*/

.numpad_sm {

	.numpad__list {
		margin-bottom: 0;
	}

	.numpad__item {
		margin: 0;
	}

	.numpad__btn {
		height: 76px;
		width: 100%;
		font-size: 36px;
		line-height: 28px;
	}

	.numpad__btn_toned {
		color: #DFDFDF;
		opacity: 0.3;
	}
}
