@import "dev/scss/helpers/variables";
@import "dev/scss/helpers/mixins";

.group-menu {
	display: inline-block;
	background: rgba(36, 39, 46, 0.5);
	box-shadow: 0px 60px 30px rgba(0, 0, 0, 0.1), 0px 30px 60px rgba(37, 40, 44, 0.3);
	backdrop-filter: blur(120px);
	border-radius: 10px;
	min-width: 150px;
	max-width: 100%;
}

.group-menu_fixed {
	position: absolute;
	top: 0;
	left: 0;
	z-index: 1;
}

.group-menu__item {

	& + & {

		.group-menu__link {
			border-top: 0.5px solid rgba(255, 255, 255, 0.1);
		}
	}
}

.group-menu__link {
	text-decoration: none;
	display: block;
	color: #fff;
	height: 40px;
	line-height: 40px;
	white-space: nowrap;
	padding: 0 12px;
}

.group-menu__title {

}

.group-menu__counter {
	font-size: 14px;
	line-height: 1;
}
