@import "dev/scss/helpers/variables";
@import "dev/scss/helpers/mixins";

.progressbar {
	position: relative;
	width: 100%;
}

.progressbar__bar {
	position: absolute;
	top: 0;
	left: 0;
	width: 0;
	height: 100%;
	z-index: 1;

	@include g-transition(background-color, width);
}

.progressbar_md {
	height: 18px;
	line-height: 18px;

	.progressbar__timer {
		line-height: inherit;
	}

	.progressbar__timer {
		display: block;
		height: 100%;
		font-size: 9px;
		position: relative;
		z-index: 2;
		padding: 0 12px;

		&:before {
			display: inline-block;
			content: '•';
			margin-right: 3px;
		}
	}
}

$statusesPalette: (
	('success', $color-success, #fff),
	('warning', $color-warning, #fff),
	('error', $color-error-dark, #fff)
);

@mixin color-status ($palette) {

	@each $modifier, $color, $text-color in $palette {

		.progressbar_#{$modifier} {
			background-color: rgba($color, .2);

			.progressbar__bar {
				background-color: $color;
			}

			.progressbar__timer {
				color: $text-color;
			}
		}
	}
}

@include color-status($statusesPalette);


/**
* Bare
*/

.progressbar_bare {
	background-color: transparent;
	height: auto;
	line-height: inherit;;

	.progressbar__timer {
		padding: 0;
		color: inherit;
	}
}

@mixin color-status-bare ($palette) {

	@each $modifier, $color in $palette {

		.progressbar_bare.progressbar_#{$modifier} {

			.progressbar__timer {
				color: $color;
			}
		}
	}
}

@include color-status-bare($statusesPalette);
