@import "dev/scss/helpers/variables";
@import "dev/scss/helpers/mixins";

.keyboard {
	position: absolute;
	bottom: 0;
	left: 0;
	right: 0;
	background: #24272E;
	color: #fff;
	padding: 10px 0;
	// height: 324px;
	// overflow: hidden;
	// display: none;
	transform: translateY(100%);

	@include g-transition(transform);

	.c-container {
		height: 100%;

		> .u-row {
			height: 100%;
		}
	}
}

.keyboard__col {
	height: 100%;
	padding-top: 10px;
	padding-bottom: 10px;
	width: 100%;

	.tiles-menu {
		height: 100%;
		overflow-y: auto;
	}
}

.keyboard__numpad {
	width: 314px;
}

.keyboard__menu {
	width: calc(100% - 314px);
}

.keyboard-toggle {
	position: absolute;
	bottom: 16px;
	left: 16px;
	font-weight: bold;
	// box-shadow: 0px 60px 30px rgba(0, 0, 0, 0.1), 0px 30px 60px rgba(37, 40, 44, 0.3);
	// backdrop-filter: blur(120px);
}

.content_show-keyboard {

	.keyboard {
		// display: block;
		transform: translateY(0);
	}
}

.keyboard-panel {
	background-color: inherit;
	border-bottom: 1px solid rgba(255, 255, 255, 0.1);
	padding: 13px 0;
	// display: none;

	.controls-list {
		margin-left: -10px;
		margin-right: -10px;
	}

	.controls-list__item {
		padding-left: 10px;
		padding-right: 10px;
	}

	.c-btn_link {

		&:hover, &:focus {
			color: $color-active-gray;
		}
	}

	.badges-list {
		margin-left: -5px;
		margin-right: -5px;
		line-height: 1;
		min-height: 20px;
	}

	.badges-list__item {
		padding-left: 5px;
		padding-right: 5px;
	}
}

.keyboard-panel__item-actions {
	width: 324px;
}

.keyboard-panel__tags {
	flex-grow: 1;
}

.keyboard__top-panel {
	position: absolute;
	left: 0;
	bottom: 100%;
	right: 0;
	transform: translateY(100%);
}

.keyboard__body {
	position: relative;
	background-color: inherit;
	height: 304px;
}

.keyboard_show-panel {

	.keyboard__top-panel {
		transform: translateY(0);
	}
}
