@import "dev/scss/helpers/variables";
@import "dev/scss/helpers/mixins";

.tables-list {

}

.tables-list__item {

	& + & {
		margin-top: 12px;
	}
}

.table-item {
	display: block;
	outline: 0;
	text-decoration: none;
	width: 36px;
	height: 36px;
	border-radius: 4px;
	position: relative;
	padding: 2px 5px;
	font-size: 13px;
	line-height: 16px;
}

.table-item_empty {
	background-color: $color-action;
	color: #fff;
}

.table-item_reserved {
	background-color: #99A1AD;
	color: #fff;
}

.table-item_opened {
	background-color: #fff;
	color: #24272E;
}

.table-item_error {
	background-color: #D11F1F;
	color: #fff;
}

.table-item_active {
	background-color: #24272E !important;
	color: #fff !important;
}

.table-item__number {
	font-weight: bold;
}

.table-item__capacity {
	text-align: right;
}


/**
* Popup
*/

.tables-list-popup {
	position: absolute;
	top: 0;
	left: 0;
	z-index: 1;
}

.table-card {
	width: 244px;
	background: #fff;
	color: #24272E;
	box-shadow: 0px 2px 4px rgba(37, 40, 44, 0.02), 0px 40px 80px rgba(37, 40, 44, 0.3);
	border-radius: 10px;

	.c-btn {
		border-top-left-radius: 0;
		border-top-right-radius: 0;
	}
}

.table-card__time {
	font-size: 10px;
	line-height: 10px;
	margin-bottom: 8px;
}

.table-card__title {
	margin-bottom: 4px;
}

.table-card__price {
	margin-top: 8px;
}

.table-card__badges {
	margin-top: 12px;
}

.table-card__body {
	padding: 12px;
}
