@import "dev/scss/helpers/variables";
@import "dev/scss/helpers/mixins";

/****************************************************************************/
/* tabs */
/****************************************************************************/

.c-tabs {
	margin-bottom: 20px;

	&:last-child {
		margin-bottom: 0;
	}
}

.c-tabs__link {
	display: block;
}

@include to-phone-lg {

	.c-tabs__tab + .c-tabs__tab {

		.c-tabs__link {
			border-top: 0;
		}
	}
}

@include from-phone-lg {

	.c-tabs__tab {
		display: inline-block;
	}

	.c-tabs__link[aria-selected] {
		position: relative;
	}
}

/**
* Default styles
*/

.c-tabs_default {

	.c-tabs__link {
		padding: 1em;
		border: 2px solid #000;
		color: #000;
	}

	.c-tabs__link[aria-selected] {
		background-color: #000;
		color: #fff;
	}

	.c-tabs__contents {
		border: 2px solid;
		padding: 1.5em;
		margin-top: -2px;
	}
}

@include to-phone-lg {

	.c-tabs_default {

		.c-tabs__link[aria-selected] {
			background: #000;
			color: #fff;
		}
	}
}

@include from-phone-lg {

	.c-tabs_default {

		.c-tabs__link {
			padding: .5em 1em;
		}

		.c-tabs__link[aria-selected] {
			border: 2px solid #000;
			background: #000;
			border-bottom: 0;
		}
	}
}
