@import "dev/scss/helpers/variables";
@import "dev/scss/helpers/mixins";

.popup-menu {
	position: absolute;
	top: 0;
	left: 0;
	display: none;
}

.popup-menu_active {
	display: block;
}

.popup-menu__item {

	& + & {
		margin-top: 8px;
	}
}

.popup-menu__link {
	box-shadow: 0px 2px 4px rgba(37, 40, 44, 0.02), 0px 40px 80px rgba(37, 40, 44, 0.3);
	backdrop-filter: blur(120px);
	min-width: 144px;
}
