/****************************************************************************/
/* FUNCTIONS */
/****************************************************************************/

@function url-friendly-color($color) {
    @return '%23' + str-slice('#{$color}', 2, -1)
};

@function rgba-to-rgb($rgba, $background: #fff) {
	@return mix(rgb(red($rgba), green($rgba), blue($rgba)), $background, alpha($rgba) * 100%)
};
