/**
*[1]. Нормализация вертикального выравнивания для Firefox
*[2]. Убрать эффект нажатия на кнопку для IE
*/
.c-acn_toggle .c-acn__content {
  max-height: 0;
  transition: max-height 0.5s cubic-bezier(0, 1, 0, 1);
  overflow: hidden;
  visibility: hidden;
  will-change: max-height;
  transform: translateZ(0);
}

.c-acn_toggle.c-acn_active .c-acn__content {
  max-height: 1000px;
  visibility: visible;
  transition: max-height 1s ease-in-out;
}

.c-acn_dropdown {
  position: relative;
}

.c-acn_dropdown .c-acn__content {
  transform: translateY(-20px);
  opacity: 0;
  visibility: hidden;
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  z-index: 2;
  transition: transform 0.3s, opacity 0.3s;
}

.c-acn_dropdown.c-acn_active .c-acn__content {
  transform: translateY(0);
  opacity: 1;
  visibility: visible;
}

/**
* Default styles
*/
.c-acn_default {
  display: block;
  margin-bottom: 20px;
}

.c-acn_default:last-child {
  margin-bottom: 0;
}

.c-acn_default .c-acn__inner {
  padding-top: 20px;
  font-size: 16px;
  line-height: 24px;
}

.c-acn_default .c-acn__toggle {
  cursor: pointer;
  display: block;
  width: 100%;
  background: none;
  text-align: left;
  outline: 0;
  position: relative;
  border-bottom: 1px solid #D2D2D2;
  padding-bottom: 15px;
}

.c-acn_default .c-acn__title {
  font-size: 20px;
  font-weight: 600;
  line-height: 27px;
}

.c-acn_default .c-acn__button {
  position: absolute;
  top: 0;
  right: 0;
  color: #D2D2D2;
}

.c-acn_default .c-acn__icon {
  width: 34px;
  height: 34px;
}

.c-acn_default.c-acn_active .c-acn__toggle {
  border-color: #288bd7;
}

.c-acn_default.c-acn_active .c-acn__button {
  color: #288bd7;
}

