.c-badge {
  display: inline-block;
  border: 0;
  text-align: center;
  max-width: 100%;
  white-space: nowrap;
  text-overflow: ellipsis;
  border-radius: 3px;
  overflow: hidden;
}

.c-badge_sm {
  height: 20px;
  line-height: 20px;
  padding: 0 4px;
  font-size: 12px;
}

.c-badge_sm .c-badge__icon {
  width: 12px;
  height: 12px;
  margin-right: 4px;
}

.c-badge_primary {
  background-color: #EBBA34;
  color: #000;
}

.c-badge_secondary {
  background-color: rgba(36, 39, 46, 0.1);
  color: #000;
}

.c-badge_tertiary {
  background-color: rgba(255, 255, 255, 0.1);
  color: #fff;
}

.c-badge_tertiary-alt {
  background-color: #DFDFDF;
  color: #000;
}

.c-badge_green {
  background-color: #95D676;
  color: #000;
}

.c-badge_red {
  background-color: #E74242;
  color: #000;
}

.c-badge_yellow {
  background-color: #FFC86D;
  color: #000;
}

.c-badge_blue {
  background-color: #4FC6E8;
  color: #000;
}

/**
* Iconed
*/
.c-badge_iconed {
  display: inline-flex;
  align-items: center;
}

/**
* Inetractive
*/
.c-badge_interactive {
  padding: 0;
}

.c-badge_interactive.c-badge_sm .c-badge__label {
  padding: 0 4px;
}

.c-badge__label {
  display: flex;
  align-items: center;
}

.c-badge__action {
  width: 12px;
  height: 12px;
  margin-left: 4px;
}

.c-badge__input:checked + .c-badge__label {
  background-color: #1B5CE0;
  color: #fff;
}

