.c-ctrl {
  margin-bottom: 10px;
  text-align: left;
  display: inline-block;
}

.c-ctrl:last-child {
  margin-bottom: 0;
}

.c-ctrl__input {
  border: 0 !important;
  clip: rect(0 0 0 0) !important;
  clip-path: inset(50%) !important;
  height: 1px !important;
  margin: -1px !important;
  overflow: hidden !important;
  padding: 0 !important;
  position: absolute !important;
  white-space: nowrap !important;
  width: 1px !important;
}

.c-ctrl__input:focus + .c-ctrl__title:before {
  border-color: #C3200A;
}

.c-ctrl__title {
  font-size: 14px;
  display: block;
  position: relative;
  line-height: 20px;
  font-weight: normal;
  position: relative;
  padding-left: 28px;
}

.c-ctrl__title:before {
  content: '';
  width: 20px;
  height: 20px;
  border: 1px solid #99A1AD;
  border-radius: 50px;
  position: absolute;
  top: 0;
  left: 0;
  background-size: 12px;
  background-repeat: no-repeat;
  background-position: center;
  transition: border-color 0.3s;
}

.c-ctrl_icon-only .c-ctrl__title {
  padding: 0;
}

.c-ctrl_icon-only .c-ctrl__title:before {
  position: static;
  display: block;
