/* Radial Point loader */
/****************************************************************************/
.loader {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: 999;
  transition: opacity .8s ease;
}

.loader_has-overlay {
  background: rgba(255, 255, 255, 0.5);
}

.loader_hidden {
  opacity: 0;
}

.loader_animated .loader__icon {
  animation: spin 1.2s infinite linear;
}

.loader__icon {
  width: 100px;
  height: 100px;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  margin: auto;
  max-height: 100%;
  max-width: 100%;
}

@keyframes spin {
  100% {
    transform: rotate(360deg);
  }
}

