* Hide overflowed text
*/
/****************************************************************************/
/* WIDTHS */
/****************************************************************************/
/**
* Generate grid classes
*/
.tiles-menu {
  position: relative;
  overflow: hidden;
}

.tiles-menu__menu {
  margin-bottom: -8px;
  overflow-y: auto;
}

.tiles-menu__menu.u-row {
  margin-left: -4px;
  margin-right: -4px;
}

.tiles-menu__item {
  padding-left: 4px;
  padding-right: 4px;
  margin-bottom: 8px;
}

.tiles-menu__item .tile-item {
  height: 100%;
}

.tiles-menu__item_opened .tiles-menu__panel {
  display: block;
  overflow: hidden scroll;
  /* Hide scrollbar for Chrome, Safari and Opera */
  /* Hide scrollbar for IE, Edge and Firefox */
}

.tiles-menu__item_opened .tiles-menu__panel::-webkit-scrollbar {
  display: none;
}

.tiles-menu__item_opened .tiles-menu__panel {
  -ms-overflow-style: none;
  /* IE and Edge */
  scrollbar-width: none;
  /* Firefox */
}

.tile-item {
  min-height: 138px;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 6px;
  text-decoration: none;
  outline: 0;
}

.tile-item__title {
  color: #fff;
  overflow: hidden;
  text-overflow: ellipsis;
}

.tile-item_1 {
  background-color: rgba(234, 182, 172, 0.88);
}

.tile-item_2 {
  background-color: rgba(110, 74, 168, 0.88);
}

.tile-item_3 {
  background-color: rgba(245, 191, 77, 0.88);
}

.tile-item_4 {
  background-color: rgba(36, 150, 129, 0.88);
}

.tile-item_5 {
  background-color: rgba(84, 188, 230, 0.88);
}

.tile-item_6 {
  background-color: rgba(191, 191, 191, 0.88);
}

.tile-item_7 {
  background-color: rgba(225, 82, 119, 0.88);
}

.tile-item_8 {
  background-color: rgba(126, 88, 74, 0.88);
}

.tile-item_light {
  background-color: rgba(223, 223, 223, 0.88);
}

.tile-item_light .tile-item__title {
  color: #000;
}

/**
* Inner panel
*/
.tiles-menu__panel {
  display: none;
  position: absolute;
  background: #24272e;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
  z-index: 1;
  padding: 0 4px;
}

.inner-menu__item + .inner-menu__item {
  margin-top: 8px;
}

.tiles-menu__panel-head {
  margin-bottom: 8px;
}

.tiles-menu__panel_active {
  display: block;
}

.tiles-menu__panel_recommendations {
  padding: 0 20px 0 0;
}

/**
* Columns
*/
.tiles-menu_columns {
  margin-bottom: 0;
}

.tiles-menu_columns .tiles-menu__item {
  margin-bottom: 0;
}

.tiles-menu_columns .tile-item {
  height: 284px;
}

.tiles-menu__panel .tiles-menu_columns .tile-item {
  height: 232px;
}

