.tiles-tablist {
  position: relative;
  padding: 0 0 66px;
  margin-top: -4px;
  margin-bottom: -4px;
  font-size: 0;
}

.tiles-tablist .slick-arrow {
  position: absolute;
  background: rgba(255, 255, 255, 0.1);
  color: #86898f;
  border-radius: 6px;
  width: 100%;
  height: 46px;
  padding: 0;
  bottom: 5px;
  outline: 0;
  z-index: 1;
}

.tiles-tablist .slick-arrow.slick-disabled {
  display: none !important;
}

.tiles-tablist .slick-icon {
  width: 16px;
  height: 16px;
}

.tiles-tablist .slick-slide {
  border: 0;
}

.tiles-tablist .slick-prev {
  display: none !important;
}

.tiles-tablist .slick-current .tiles-tablist__tile {
  background: #1B5CE0;
  color: #fff;
}

.tiles-tablist__list {
  height: 100%;
  position: relative;
}

.tiles-tablist__item {
  padding: 4px 0;
}

.tiles-tablist__tile {
  display: block;
  background: rgba(255, 255, 255, 0.1);
  border-radius: 6px;
  color: #86898F;
  border: 0;
  outline: 0;
  width: 100%;
  height: 105px;
  text-align: center;
  position: relative;
}

