@import "dev/scss/helpers/variables";
@import "dev/scss/helpers/mixins";

.order-form {
	background: #fff;
	box-shadow: 0px 30px 60px rgba(37, 40, 44, 0.3), 0px 60px 30px rgba(37, 40, 44, 0.1);
	backdrop-filter: blur(120px);
	border-radius: 16px;
	width: 508px;
	height: 736px;
	overflow: hidden;
	position: relative;

	display: flex;
	flex-direction: column;

	.order-btn {
		min-width: 130px;
	}
}

.order-form__head {
	padding: 16px 20px;
	border-bottom: 0.5px solid rgba(0, 0, 0, 0.1);

	flex: 0 0 auto;
}

.order-form__inner {
	flex: 1 0 0;
	position: relative;
	overflow: hidden;
}

.order-form__body {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	padding: 12px 20px 30px;
}

// .order-form__body-inner {
// 	max-height: 300px;
// 	overflow-y: auto;
// }

.order-form__info {
	color: #99A1AD;
}

.order-form__top {
	margin-bottom: 8px;
}

.order-section__head {
	margin-bottom: 9px;
	min-height: 22px;
}

.order-section__controls {
	margin-top: 12px;
}

/**
* Filter
*/

.order-filter {
	margin-left: -6px;
	margin-right: -6px;
}

.order-filter__item {
	padding-left: 6px;
	padding-right: 6px;
}


/**
* Note
*/

.order-form__note {
	background-color: rgba(36, 39, 46, 0.7);
	backdrop-filter: blur(60px);
	color: #fff;
	padding: 16px 24px;
}

.order-form__note_fixed {
	position: absolute;
	z-index: 2;
	top: 0;
	left: 0;
	right: 0;
}


/**
* Payment
*/

.order-form_payment {

	.order-section__head {
		margin-bottom: 20px;
	}
}

.order-form__controls {

	.u-row {
		margin-left: -4px;
		margin-right: -4px
	}

	.order-form__ctrl {
		padding-left: 4px;
		padding-right: 4px;
	}
}

.order-section__section {

	& + & {
		margin-top: 16px;
	}

	.c-title {
		margin-bottom: 12px;
	}

	.badges-list {
		margin-left: -4px;
		margin-right: -4px;
	}

	.badges-list__item {
		padding-left: 4px;
		padding-right: 4px;
		margin-bottom: 8px;
	}
}
