@import "dev/scss/helpers/variables";
@import "dev/scss/helpers/mixins";

/****************************************************************************/
/* accordeon */
/****************************************************************************/

/**
*[1]. Нормализация вертикального выравнивания для Firefox
*[2]. Убрать эффект нажатия на кнопку для IE
*/

// default animation
.c-acn_toggle {

	.c-acn__content {

		@include toggle-up;
	}

	&.c-acn_active {

		.c-acn__content {

			@include toggle-down;
		}
	}
}

// dropdown animation
.c-acn_dropdown {
	position: relative;

	.c-acn__content {
		transform: translateY(-20px);
		opacity: 0;
		visibility: hidden;
		position: absolute;
		top: 100%;
		left: 0;
		right: 0;
		z-index: 2;

		@include g-transition(transform, opacity)
	}

	&.c-acn_active {

		.c-acn__content {
			transform: translateY(0);
			opacity: 1;
			visibility: visible;
		}
	}
}


/**
* Default styles
*/

.c-acn_default {

	$icon-width: 30px;
	$icon-height: 30px;

	display: block;
	margin-bottom: 20px;

	&:last-child {
		margin-bottom: 0;
	}

	.c-acn__inner {
	// 	padding: 30px 20px 20px;
		padding-top: 20px;
		font-size: 16px;
		line-height: 24px;
	}

	.c-acn__toggle {
		cursor: pointer;
		display: block;
		width: 100%;
		background: none;
		text-align: left;
		outline: 0;
		position: relative;
		border-bottom: 1px solid #D2D2D2;
		padding-bottom: 15px;
	}

	.c-acn__title {
		font-size: 20px;
		font-weight: 600;
		line-height: 27px;
	}

	.c-acn__button {
		position: absolute;
		top: 0;
		right: 0;
		color: #D2D2D2;
	}

	.c-acn__icon {
		width: 34px;
		height: 34px;
	}

	&.c-acn_active {

		.c-acn__toggle {
			border-color: $brand-color;
		}

		.c-acn__button {
			color: $brand-color;
		}
	}
}
