@import "dev/scss/helpers/variables";
@import "dev/scss/helpers/mixins";

$border-width: 1px;

.c-input {
	display: block;
	width: 100%;
	outline: 0;
	font-family: inherit;

	@include input-placeholder {
		color: #8b8a89;
	}
}


/**
* Default
*/

.c-input_default {
	border: 0;
	border: $border-width solid #D2D2D2;
	background: 0;
	color: $text-color;
	border-radius: 20px;

	@include g-transition(border-color);

	&:focus {
		border-color: $sec-color;
	}

	// &.c-input_error {
	// 	@include status($color-error, 30%);
	// }

	// &.c-input_success {
	// 	@include status($color-success, 30%);
	// }
}


/**
* Small
*/

.c-input_sm {
	height: 24px;
	padding: 0 8px;
	font-size: 14px;

	&.c-input_default {
		border-radius: 3px;
	}
}


/**
* Medium
*/

.c-input_md {
	height: 40px;
	font-size: 14px;
	padding: 0 20px;

	&.c-input_multiline {
		min-height: 40px;
		padding-top: 10px;
		padding-bottom: 10px;
		height: 110px;
	}
}


/**
* Hidden
*/

.c-input_phone {
	@include hide;
}


/**
* Label
*/

.c-input-label {
	display: inline-block;
	color: inherit;
	font-weight: normal;
	font-size: 14px;
	margin-bottom: 10px;
}

.c-input-label_secondary {
	color: $sec-color;
}
