@import "dev/scss/helpers/variables";
@import "dev/scss/helpers/mixins";

.side-menu__item {

	& + & {

		.side-menu__link:before {
			content: '';
			position: absolute;
			border-top: 0.5px solid rgba(0, 0, 0, 0.1);
			width: 28px;
			top: 0;
			left: 50%;
			margin-left: -14px;
		}
	}
}

.side-menu__link {
	display: block;
	text-decoration: none;
	text-align: center;
	color: #24272E;
	position: relative;
	padding: 16px 5px 12px;
	outline: 0;
}

.side-menu__thumb {
	margin-bottom: 14px;
	height: 24px;
}

.side-menu__text {
	white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
}


/**
* Active
*/

.side-menu__item_active {

	.side-menu__link {
		background: #24272E;
		color: #DFDFDF;
	}
}
