@import "dev/scss/helpers/variables";
@import "dev/scss/helpers/mixins";

.main-menu {

}

.main-menu__item {

	& + & {
		margin-top: 8px;
	}
}

.main-menu__item_active {

	.main-menu__link {
		background-color: #24272E;
		color: #DFDFDF;
	}
}

.main-menu__link {
	display: block;
	color: #24272E;
	background-color: rgba(37, 40, 44, 0.1);
	backdrop-filter: blur(120px);
	text-align: center;
	padding: 36px 10px 20px;
	border-radius: 10px;
	text-decoration: none;

	&:active {
		background-color: #24272E;
		color: #DFDFDF;
	}
}

.main-menu__icon {
	stroke: none;
	width: 48px;
	height: 48px;
	margin-bottom: 28px;
}

.main-menu__text {
	font-size: 17px;
	line-height: 20px;
	font-weight: bold;

}
