@import "dev/scss/helpers/variables";
@import "dev/scss/helpers/mixins";

.tiles-menu {
	position: relative;
	overflow: hidden;



}

.tiles-menu__menu {
	margin-bottom: -8px;
	overflow-y: auto;

	&.u-row {
		margin-left: -4px;
		margin-right: -4px;
	}
}

.tiles-menu__item {
	padding-left: 4px;
	padding-right: 4px;
	margin-bottom: 8px;

	.tile-item {
		height: 100%;
	}
}

.tiles-menu__item_opened {

	.tiles-menu__panel {
		display: block;
		overflow: hidden scroll;

		@include hide-scrollbar;
	}
}

.tile-item {
	min-height: 138px;
	display: flex;
	justify-content: center;
	align-items: center;
	background: rgba(255, 255, 255, 0.1);
	border-radius: 6px;
	text-decoration: none;
	outline: 0;
}

.tile-item__title {
	color: #fff;
	overflow: hidden;
	text-overflow: ellipsis;
}

$tilesPalette: (
	('1', #EAB6AC),
	('2', #6E4AA8),
	('3', #F5BF4D),
	('4', #249681),
	('5', #54BCE6),
	('6', #BFBFBF),
	('7', #E15277),
	('8', #7E584A),
	('light', #DFDFDF)
);

@mixin color-tile ($palette) {

	@each $modifier, $color in $palette {

		.tile-item_#{$modifier} {
			background-color: rgba($color, .88);
		}
	}
}

@include color-tile($tilesPalette);

.tile-item_light {

	.tile-item__title {
		color: #000;
	}
}


/**
* Inner panel
*/

.tiles-menu__panel {
	display: none;
	position: absolute;
	background: #24272e;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	overflow: hidden;
	z-index: 1;
	padding: 0 4px;
}

.inner-menu__item {

	& + & {
		margin-top: 8px;
	}
}

.tiles-menu__panel-head {
	margin-bottom: 8px;
}

.tiles-menu__panel_active {
	display: block;
}

.tiles-menu__panel_recommendations {
	padding: 0 20px 0 0;
}


/**
* Columns
*/

.tiles-menu_columns {
	margin-bottom: 0;

	.tiles-menu__item {
		margin-bottom: 0;
	}

	.tile-item {
		height: 284px;
	}
}

.tiles-menu__panel {

	.tiles-menu_columns {

		.tile-item {
			height: 232px;
		}
	}
}
