@import "dev/scss/helpers/variables";
@import "dev/scss/helpers/mixins";

.notifications-list {

}

.notifications-list__item {

	& + & {
		margin-top: 8px;
	}
}

.notification-item {
	background: rgba(36, 39, 46, 0.7);
	color: #fff;
	border-radius: 10px;
	padding: 12px;

	.c-badge_secondary {
		background-color: #fff;
	}
}

.notification-item_error {
	background-color: $color-error;
}

.notification-item_warning {
	background-color: $color-warning;
}

.notification-item_success {
	background-color: $color-success;
}

.notification-item__head {
	margin-bottom: 8px;
}

.notification-item__action {
	font-size: 11px;
	line-height: 12px;
	letter-spacing: -.3px;
	text-transform: uppercase;
}

.notification-item__title {
	font-weight: 600;
}

.notification-item__desc {
	font-size: 11px;
	line-height: 14px;
	letter-spacing: -.3px;
	margin-bottom: 5px;
}

.notification-item_table {

	.notification-item__title {
		margin-bottom: 9px;
	}
}

.notification-item_type1 {

	.notification-item__title {
		margin-bottom: 6px;
	}
}

.notification-item_type2 {

	.notification-item__title {
		color: #000;
		font-size: 15px;
		line-height: 18px;
	}

	.notification-item__table {
		font-size: 11px;
		line-height: 12px;
		letter-spacing: -.1px;
		margin-bottom: 10px;
	}
}
