@import "dev/scss/helpers/variables";
@import "dev/scss/helpers/mixins";

.tiles-tablist {
	position: relative;
	// padding: 0 5px 66px;
	padding: 0 0 66px;
	// height: 100%;
	margin-top: -4px;
	margin-bottom: -4px;
	font-size: 0;
	// margin: -4px 5px;

	.slick-arrow {
		position: absolute;
		background: rgba(255, 255, 255, 0.1);
		color: #86898f;
		border-radius: 6px;
		width: 100%;
		height: 46px;
		padding: 0;
		bottom: 5px;
		outline: 0;
		z-index: 1;

		&.slick-disabled {
			display: none !important;
		}
	}

	.slick-icon {
		width: 16px;
		height: 16px;
	}

	.slick-slide {
		border: 0;
	}

	.slick-prev {
		display: none !important;
	}

	.slick-current .tiles-tablist__tile {
		background: $color-action;
		color: #fff;
	}
}

.tiles-tablist__list {
	height: 100%;
	position: relative;
}

.tiles-tablist__item {
	padding: 4px 0;
}

.tiles-tablist__tile {
	display: block;
	background: rgba(255, 255, 255, 0.1);
	border-radius: 6px;
	color: #86898F;
	border: 0;
	outline: 0;
	width: 100%;
	height: 105px;
	text-align: center;
	position: relative;
}

.tiles-tablist__icon {
	width: 20px;
	height: 20px;
}
